body {
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.search-form {
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  position: relative;
}

input[type="text"] {
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 0;
  padding: 8px;
  font-size: 16px;
  transition: border-color .3s;
}

input[type="text"]:hover, input[type="text"]:focus, .search-button:hover, .search-button:focus {
  border-color: #000;
  outline: none;
}

.search-button {
  cursor: pointer;
  background-color: #ddd;
  border: none;
  outline: 1px solid rgba(0, 0, 0, 0);
  align-items: center;
  padding: 8px;
  transition: background-color .3s;
  display: flex;
}

.search-button svg {
  fill: #333;
}

.search-button:hover, .search-button:focus {
  border: 1px solid #000;
  outline: none;
}

.header {
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  background-color: #146dac;
  margin: 0;
  padding: 0;
}

.column-container {
  flex-wrap: wrap;
  flex-basis: 100%;
  gap: 20px;
  display: flex;
}

.column {
  margin-bottom: 20px;
}

.image-container {
  height: 250px;
  overflow: hidden;
}

.image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.description {
  flex-direction: column;
  display: flex;
}

.info-item {
  margin-bottom: 5px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
}

